.error-container {
    flex-direction: column;
    display: flex;
    justify-content: center;
    align-items: center;
}

.error-text {
    font-size: 4rem;
    font-weight: bold;
    color: SlateBlue;
    margin: 10px;
}

.error-img {
    max-width: 50%;
    max-height: 50%;
    aspect-ratio: initial;
}
