.neah-text {
    font-size: 4rem;
    font-weight: bold;
    color: SlateBlue;
    margin: 10px;
}

.neah-img {
    max-width: 50%;
    max-height: 50%;
    aspect-ratio: initial;
    animation: hinge 2.5s ease forwards;
}

.start-page-button-text {
    font-size: 20px;
    font-weight: bold;
}

@keyframes hinge {
    0% {
        transform: rotate(0);
        transform-origin: top left;
        animation-timing-function: ease-in-out;
    }
    20%,
    60% {
        transform: rotate(80deg);
        transform-origin: top left;
        animation-timing-function: ease-in-out;
    }
    40% {
        transform: rotate(60deg);
        transform-origin: top left;
        animation-timing-function: ease-in-out;
    }
    80% {
        transform: rotate(60deg) translateY(0);
        opacity: 1;
        transform-origin: top left;
        animation-timing-function: ease-in-out;
    }
    100% {
        transform: translateY(700px);
        opacity: 0;
    }
}
